.Wui__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 2rem 6rem;
}

.Wui__navbar-links {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.Wui__navbar-links_logo {
  margin-right: 2rem;
}
.Wui__navbar-links_logo img {
  width: 62.56px;
  height: 16.02px;
}
.Wui__nav-links_container {
  display: flex;
  flex-direction: row;
}
.Wui__navbar-sign {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.Wui__nav-links_container p,
.Wui__navbar-sign p,
.Wui__navbar-menu_container p {
  color: #fff;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 25px;
  text-transform: capitalize;

  margin: 0 1rem;
  cursor: pointer;
}
.Wui__navbar-sign button,
.Wui__navbar-menu_container button {
    padding: 0.5rem 1rem;
  background-color: #ff4820;
  color: #fff;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 25px;
  border-radius: 5px;
  border: 0;
  outline: 0;
  cursor: pointer;
}
.Wui__navbar-menu {
  margin-left: 1rem;

  display: none;
  position: relative;
}

.Wui__navbar-menu svg{
  cursor: pointer;
}

.Wui__navbar-menu_container{
  display: flex;
  justify-content: flex-end;
  flex-direction: column;

  text-align: end;
  background-color: var(--color-footer);
  padding: 2rem;
  position: absolute;
  top: 40px;
  right: 0;
  margin-top: 1rem;
  border-radius: 5px;
  min-width: 210px;
  box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.Wui__navbar-menu_container p{
  margin: 1rem 0 ;
}
.Wui__navbar-menu_container-links-sign {
  display: none;
}

@media screen and (max-width :1050px) {
  .Wui__nav-links_container{
    display: none;
  }
  .Wui__navbar-menu{
    display: flex;
  }
}

@media screen and (max-width : 700px) {
  .Wui__navbar {
    padding: 2rem 4rem;
  }
}
@media screen and (max-width:550px){
  .Wui__navbar{
    padding: 2rem;
  }
  .Wui__navbar-sign {
    display: none;
  }
  .Wui__navbar-menu_container{
    top: 20px
  }
  .Wui__navbar-menu_container-links-sign{
    display: block;
  }
}